import { TokenStorageService } from './../_services/token-storage.service';
import { AlertService } from "./../_services/alert.service";
import { AuthenticationService } from "./../_services/authentication.service";
import { LoginService } from "./login.service";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, FormBuilder } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { first } from "rxjs/operators";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
  providers: [LoginService],
})
export class LoginComponent implements OnInit {
  rfContact: FormGroup;
  loading = false;
  submitted = false;
  returnUrl: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder,
    private authenticationService: AuthenticationService,
    private alertService: AlertService,
    private tokenStorageService: TokenStorageService
  ) {
    if (this.tokenStorageService.currentUser) {
      this.router.navigate(["/"]);
    }
  }
  ngOnInit() {
    this.rfContact = this.fb.group({
      username: new FormControl(),
      password: new FormControl(),
    });
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"] || "/";
  }
  addPost(loginModel) {
    this.submitted = true;
    // stop here if form is invalid
    if (this.rfContact.invalid) {
      return;
    }
    this.loading = true;
    this.authenticationService.login(loginModel.username, loginModel.password).pipe(first())
      .subscribe(
        data => {
          let item = JSON.parse(JSON.stringify(data));
          if (item.err == null) {
            this.tokenStorageService.saveToken(item.jwrRes.token);
            this.tokenStorageService.saveUser(item.jwrRes.user);
            console.log(this.returnUrl);
            this.router.navigate([this.returnUrl]);
          } else {
            this.loading = false;
            console.log(item.err.depcription);
          }
        },
        error => {
          this.loading = false;
          console.log(error);
        });
  }
}
